<template>
  <div class="app">
    <div v-if="state.account.nickname" id="iyyoo_logined">
      <p>환영합니다! {{ state.account.nickname }}님</p>
      <!-- <button @click="logout()">로그아웃</button>
      <button @click="sudokustart=true;">스도쿠 게임하기</button>
      <button @click="snakestart=true;">스네이크 게임하기</button>
      <button @click="rank=true;">랭킹보기</button> -->
      <div id="menu_bar">
      <button class="w-btn w-btn-indigo" type="button" @click="logout()">로그아웃</button>
      <button class="w-btn w-btn-indigo" type="button" @click="sudokustart=true;">스도쿠 게임하기</button>
      <button class="w-btn w-btn-indigo" type="button" @click="snakestart=true;">스네이크 게임하기</button>
      <button class="w-btn w-btn-indigo" type="button" @click="rank=true;">랭킹보기</button>
      </div>
      <MyTestGameView 
        v-if="sudokustart"
        @inputdata="sendsudoku"
        @endsudokus="sudokustart=false;"
      />
    </div>

    <div v-else id="iyyoo_login">
    <label for="nickname">
        <h2>
          <span class="entypo-login">
            <i class="fa fa-sign-in">
            </i>
          </span> 
          Login
        </h2>
        <button @click="submit()">
          <span class="entypo-lock">
            <i class="fa fa-lock">
            </i>
          </span>
        </button>
        <span class="entypo-user inputUserIcon">
          <i class="fa fa-user"></i>
        </span>
        <input type="text" class="user" id="nickname" v-model="state.form.nickname" placeholder="username"/>
    </label>
    </div>

  </div>
</template>

<script>
import { inject } from "vue";
import { reactive } from "vue";
import axios from 'axios';
import MyTestGameView from "./components/MyTestGameView.vue"

export default {
  name:"App",
  components:{
    MyTestGameView,
  },
  methods:{
    sendsudoku(timer){
      let nickname = this.state.account.nickname
      let url = 'http://localhost:3001/testa'
      axios({
        url:url,
        method:"PUT",
        data:{
          nickname:nickname,
          timer:timer
        }
      }).then( (res) => {
        console.log(res)
      })
    },
  },
  data(){
    return {
      sudokustart:false,
    }
  },
  setup() {
    const axios = inject('$axios');
    const state = reactive({
      account: {
        id: null,
        // name: "",
      },
      form: {
        nickname: "",
      },
    });

    const submit = () => {
      const args = {
        nickname: state.form.nickname,
      };
      axios
        .post("http://localhost:3001/api/account", args)
        .then((res) => {
          alert("로그인에 성공했습니다.");
          state.account.nickname = res.data;
          
        })
        .catch(() => {
          console.log(args);
          alert("로그인에 실패했습니다. 계정 정보를 확인해주세요.");
        });
    };

    const logout = () => {
      axios.delete("http://localhost:3001/api/account").then(() => {
        alert("로그아웃하였습니다.");
        state.account.nickname = null;
        state.form.nickname = '';
        location.reload();
      });
    };

    return { state, submit, logout };
  },
};
</script>

